// https://github.com/diegohaz/arc/wiki/API-service
import isEmpty from 'lodash/isEmpty'
import 'whatwg-fetch'
import { stringify } from 'query-string'
import merge from 'lodash/merge'
import config from '../config'
import { getBrowserLocale } from './funcs'

export const METHOD_GET = 'get'
export const METHOD_POST = 'post'
export const METHOD_PUT = 'put'
export const METHOD_PATCH = 'patch'
export const METHOD_DELETE = 'delete'

export const checkStatus = async (response) => {
  if (response.status === 204) {
    return null
  }

  const contentType = response.headers.get('Content-Type')

  const data = contentType && contentType.indexOf('application/json') >= 0 ? await response.json() : await response.text()

  if (response.status >= 200 && response.status < 300 && response.ok) {
    return data
  }

  const err = new Error(data.message)
  err.msgId = data.msgId
  err.msgArgs = data.msgArgs
  err.titleId = data.titleId
  err.titleArgs = data.titleArgs
  err.userLanguage = data.userLanguage
  err.translatedMessage = data.translatedMessage
  err.translatedTitle = data.translatedTitle
  throw err
}

export const parseSettings = ({
  method = METHOD_GET, data, ...otherSettings
} = {}) => {
  const headers = {
    Accept: 'application/json',
    'Accept-Language': getBrowserLocale(),
    'Access-Control-Allow-Origin': '*'
  }
  if (method !== METHOD_GET) {
    headers['Content-Type'] = 'application/json'
  }
  const settings = merge({
    body: data ? JSON.stringify(data) : undefined,
    method,
    headers,
  }, otherSettings)

  return settings
}

export const parseEndpoint = (endpoint, params = {}, apiBaseUrl = config.apiUrl) => {
  const url = endpoint.indexOf('http') === 0 ? endpoint : apiBaseUrl + endpoint

  const idToken = localStorage.getItem('idToken')
  if (idToken) params.access_token = idToken
  const querystring = !isEmpty(params) ? `?${stringify(params)}` : ''
  return `${url}${querystring}`
}

const api = {}

api.request = (endpoint, { baseUrl, params, ...settings } = {}) =>
  fetch(parseEndpoint(endpoint, params, baseUrl), parseSettings(settings))
    .then(checkStatus);
[METHOD_DELETE, METHOD_GET].forEach((method) => {
  api[method] = (endpoint, settings) => api.request(endpoint, { method, ...settings })
});
[METHOD_POST, METHOD_PUT, METHOD_PATCH].forEach((method) => {
  api[method] = (endpoint, data, settings) => api.request(endpoint, { method, data, ...settings })
})

api.create = (settings = {}) => ({
  settings,

  setToken(token) {
    this.settings.headers = {
      ...this.settings.headers,
      // Authorization: `Bearer ${token}`,
      'x-access-token': token,
    }
  },

  unsetToken() {
    this.settings.headers = {
      ...this.settings.headers,
      // Authorization: undefined,
      'x-access-token': undefined,
    }
  },

  request(endpoint, options) {
    return api.request(endpoint, merge({}, this.settings, options))
  },

  post(endpoint, data, options) {
    return this.request(endpoint, { method: METHOD_POST, data, ...options })
  },

  get(endpoint, options) {
    return this.request(endpoint, { method: METHOD_GET, ...options })
  },

  put(endpoint, data, options) {
    return this.request(endpoint, { method: METHOD_PUT, data, ...options })
  },

  patch(endpoint, data, options) {
    return this.request(endpoint, { method: METHOD_PATCH, data, ...options })
  },

  delete(endpoint, options) {
    return this.request(endpoint, { method: METHOD_DELETE, ...options })
  },
})

export const getResponseBody = async (response) => {
  if (response.status === 204) {
    return null
  }

  const contentType = response.headers.get('Content-Type')

  let data

  if (contentType && contentType.indexOf('application/json') >= 0) {
    data = await response.json()
  } else if (contentType && contentType.indexOf('text/csv') >= 0) {
    data = await response.blob()
  } else {
    data = await response.text()
  }

  if (response.status >= 200 && response.status < 300 && response.ok) {
    return data
  }

  throw data
}

export default api
