import React from 'react'
import { PXListing } from '../../components/_common'
import axios from 'axios'
import { getLocalizedPath } from "../../utils/funcs"

class PXUsers extends React.Component {
  constructor(props) {
    super(props)
    
    this.state = {
      users: [],
      loading: false
    }
  }
  
  componentDidMount() {
    this.fetchUsers();
  }
  
  fetchUsers() {
    this.setState({loading: true})
    
    axios
      .get("https://jsonplaceholder.typicode.com/users")
      .then(response => {
        const newUsers = response.data.map(u => {
          return {
            id: u.id,
            name: u.name,
            email: u.email,
            phone: u.phone
          };
        });
        const newState = Object.assign({}, this.state, {
          users: newUsers
        });
        this.setState(
          newState,
        )
        this.setState({loading: false})
      })
      .catch(error => console.log(error));
  }
  
  onEditRow = (row) => {
    this.props.history.push({
      pathname: getLocalizedPath(`/edit-user`, this.props.intl.locale),
      search: `?id=` + row,
    })
  }
  
  onSuspendRow = (row) => {
    console.info(row)
  }
  
  onReassignRow = (row) => {
    console.info(row)
  }
  
  renderListingTable() {
    const {
      intl: {
        messages
      }
    } = this.props
    
    const {
      loading,
      users
    } = this.state
    
    const listHead = [
      {
        key: `name`,
        label: messages.list_header.userName,
        sortable: true,
        order: 'asc'
      },
      {
        key: `email`,
        label: messages.list_header.userEmail,
        sortable: true,
        order: 'asc'
      },
      {
        key: `phone`,
        label: messages.list_header.userPhone,
        sortable: true,
        order: 'asc'
      },
    ]
    
    const listActions = [
      {
        key: `edit`,
        label: `Edit`,
        callback: this.onEditRow
      },
      {
        key: `suspend`,
        label: `Suspend`,
        callback: this.onSuspendRow
      },
      {
        key: `reassign`,
        label: `Reassign`,
        callback: this.onReassignRow
      }
    ]
    
    const sortObject = [
      {
        key: 'name',
        label: messages.listing.sorting.name
      },
      {
        key: 'email',
        label: messages.listing.sorting.email
      },
      {
        key: 'phone',
        label: messages.listing.sorting.phone
      }
    ]
    return (
      <PXListing
        data={users}
        mappingObj={listHead}
        mappingActions={listActions}
        fullTextSearch={true}
        sortObject={sortObject}
      />
    )
  }
  
  render() {
    const {
      loading
    } = this.state
    
    return (
      <section className='page'>
        <section className='users with-bkg'>
          <section className='uk-container'>
            {!loading && this.renderListingTable()}
          </section>
        </section>
      </section>
    )
  }
}

export default (PXUsers)