import React from 'react'
import PropTypes from 'prop-types'
import {injectIntl} from 'react-intl'
import SimpleReactValidator from 'simple-react-validator';
import Select from 'react-select'
import {COUNTRIES, FRUIT_TYPES, USERS} from '../../utils/constants'
import config from '../../config'
import { PXModal } from '../_common'
import PXAddUserForm from '../add-user'
import { addUser, updateUser } from "../../redux/user/actions"

import './style.css'

class PXAddCorporate extends React.Component {
  constructor(props) {
    super(props)
    
    this.state = {
      corporateId: this.props.corporateId || null,
      name: 'Ovidiu Rugina Corp',
      countries: [],
      fruitTypes: [],
      credit: 200,
      users: [],
      modalOpen: false,
    }
  }
  
  componentWillMount() {
    const {
      intl: {
        locale,
        messages
      }
    } = this.props
  
    this.validator = new SimpleReactValidator({
      autoForceUpdate: this,
      className: 'uk-text-danger',
      locale: `${locale}`,
      messages: {
        required: messages.add_corporate.req.required,
        alpha: messages.add_corporate.req.alpha,
        email: messages.add_corporate.req.email,
        phone: messages.add_corporate.req.phone,
        min: messages.add_corporate.req.min,
      },
    });
  }
  
  submitForm = () => {
    if (this.validator.allValid()) {
      this.props.onSubmit(this.state)
    } else {
      this.validator.showMessages();
      this.forceUpdate();
    }
  }
  
  onChangeInput = (e) => {
    this.setState({
      [`${e.target.name}`]: e.target.value
    })
  }
  
  onChangeSelect = (value, label) => {
    this.setState({
      [`${label}`]: value
    })
  }
  
  onRequestClose = () => {
    this.setState({ modalOpen: false })
  }
  
  handleAddUserSubmit = async (formValues) => {
    if(!formValues.corporateId){
      addUser(formValues)
    } else {
      updateUser(formValues)
    }
  }
  
  render() {
    const {
      intl: {
        messages
      },
    } = this.props
    
    const {
      name,
      country,
      fruitType,
      credit,
      users,
      modalOpen
    } = this.state
    
    return (
      <div className='form-holder'>
        <div className='section-header'>Add a new corporate</div>
        <form className="add-user-form uk-flex uk-flex-column" action="" name="">
          <div className="elements-group uk-flex uk-flex-column">
            <div className="uk-grid-colapse">
              <div className="input-holder form-group uk-width-1-1 uk-child-width-1-2 uk-flex uk-flex-middle">
                <label htmlFor='name'>{messages.add_corporate.name}</label>
                <input
                  type="text"
                  name="name"
                  className="form-control"
                  value={name}
                  placeholder={messages.add_corporate.name}
                  onChange={(e) => this.onChangeInput(e)}
                  onBlur={() => this.validator.showMessageFor('name')}
                />
                {this.validator.message('name', name, 'required')}
              </div>
              <div className="input-holder form-group uk-width-1-1 uk-child-width-1-2 uk-flex uk-flex-middle">
                <label htmlFor='country'>{messages.add_corporate.country}</label>
                <Select
                  name='country'
                  className="form-control"
                  classNamePrefix="select"
                  options={COUNTRIES}
                  value={country}
                  isMulti
                  onChange={(e) => this.onChangeSelect(e, 'countries')}
                />
                {this.validator.message('country', country, 'required')}
              </div>
            </div>
  
            <div className="uk-grid-colapse">
              <div className="input-holder form-group uk-width-1-1 uk-child-width-1-2 uk-flex uk-flex-middle">
                <label htmlFor='fruitType'>{messages.add_corporate.fruitType}</label>
                <Select
                  name='fruitType'
                  className="form-control"
                  classNamePrefix="select"
                  options={FRUIT_TYPES}
                  value={fruitType}
                  isMulti
                  onChange={(e) => this.onChangeSelect(e, 'fruitType')}
                />
                {this.validator.message('fruitType', fruitType, 'required')}
              </div>
              <div className="input-holder form-group uk-width-1-1 uk-child-width-1-2 uk-flex uk-flex-middle">
                <label htmlFor='credit'>{messages.add_corporate.credit}</label>
                <input
                  type="text"
                  name="credit"
                  className="form-control"
                  value={credit}
                  placeholder={messages.add_corporate.credit}
                  onChange={(e) => this.onChangeInput(e)}
                  onBlur={() => this.validator.showMessageFor('credit')}
                />
                {this.validator.message('credit', credit, 'required')}
              </div>
            </div>
  
            <div className="uk-grid-colapse">
              <div className="input-holder form-group uk-width-1-1 uk-child-width-1-2 uk-flex uk-flex-middle">
                <label htmlFor='users'>{messages.add_corporate.users}</label>
                <Select
                  name='users'
                  className="form-control"
                  classNamePrefix="select"
                  options={USERS}
                  value={users}
                  isMulti
                  onChange={(e) => this.onChangeSelect(e, 'users')}
                />
                {this.validator.message('users', users, 'required')}
              </div>
  
              <div className="input-holder form-group uk-width-1-1 uk-child-width-1-2 uk-flex uk-flex-middle">
                <label htmlFor='add-users' onClick={() => this.setState({modalOpen: !modalOpen})}>{messages.add_corporate.addUser}</label>
                
              </div>
            </div>
  
            <div className='button-holder uk-width-1-2@s uk-width-1-1'>
              <div className='btns' onClick={() => this.submitForm()}>{messages.add_corporate.submit}</div>
            </div>
          </div>
        </form>
  
        <PXModal isOpen={modalOpen} onRequestClose={this.onRequestClose}>
          <PXAddUserForm
            onSubmit = {this.handleAddUserSubmit}
            inModal = {true}
          />
        </PXModal>
      </div>
    )
  }
}

PXAddCorporate.propTypes = {
  onSubmit: PropTypes.func
}

export default injectIntl(PXAddCorporate)