import config from '../../config'
import { getResponseBody } from '../../utils/api'
import { notificationError } from "../notification/actions";

export const USER_SET_CURRENT_USER = 'USER_SET_CURRENT_USER'
export function loginUser(user) {
  return async (dispatch, getState) => {
    try {
      const headers = {
        'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json',
      }
      
      const response = await fetch(`${config.apiUrl}/token-auth`, {
        method: 'POST',
        headers: headers,
        body: JSON.stringify({
          username: user.username,
          password: user.password
        }),
      })

      const body = await getResponseBody(response)
      dispatch({
        type: 'USER_SET_CURRENT_USER',
        payload: body,
      })

      return {
        ...body,
        status: response.status
      }
    } catch (error) {
      dispatch(notificationError(error))
    }
    return null;
  }
}

export function getUser() {
  return async (dispatch, getState) => {
    try {
      const {
        token,
      } = JSON.parse(localStorage.getItem('currentUser'))

      const response = await fetch(`${config.apiUrl}/user`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + token
        },
      })

      const body = await getResponseBody(response)
      dispatch({
        type: 'USER_SET_CURRENT_USER',
        payload: {
          ...body,
          token: token
        },
      })

      return {
        ...body,
        status: response.status
      }
    } catch (error) {
      dispatch(notificationError(error))
    }
    return null;
  }
}

export function logoutUser() {
  return async (dispatch, getState) => {
    try {
      dispatch({
        type: 'USER_SET_CURRENT_USER',
        payload: {},
      })
    } catch (error) {
      dispatch(notificationError(error))
    }
    return null;
  }
}

