import React from 'react'
import {withRouter} from 'react-router-dom'
import { injectIntl } from 'react-intl'
import { connect } from 'react-redux'
import PXAddHoldingForm from '../../components/add-holding'
import { addHolding, updateHolding } from "../../redux/holding/actions"

class PXAddHolding extends React.Component {
  constructor(props) {
    super(props)
    
    this.state = {}
  }
  
  handleSubmit = async (formValues) => {
    if(!formValues.holdingId){
      addHolding(formValues)
    } else {
      updateHolding(formValues)
    }
  }
  
  render() {
    const {
      intl: {
        messages, locale
      },
    } = this.props
    
    const {
      terms,
      isLoading
    } = this.state
    
    return (
      <section className="forms-container with-bkg">
        <section className='new-user full-height small-pages uk-container uk-height-1-1 uk-flex uk-flex-column uk-flex-center'>
          <div className='uk-width-2-3@m uk-width-1-1'>
          <PXAddHoldingForm
            onSubmit = {this.handleSubmit}
          />
        </div>
      </section>
      </section>
    )
  }
}

const mapStateToProps = state => ({})

const mapDispatchToProps = dispatch => ({
  addHolding: data => dispatch(addHolding(data)),
  updateHolding: data => dispatch(updateHolding(data)),
})

export default withRouter(injectIntl(connect(mapStateToProps, mapDispatchToProps)(PXAddHolding)))