import React from 'react'
import {withRouter} from 'react-router-dom'
import { injectIntl } from 'react-intl'
import { connect } from 'react-redux'
import PXAddOrchardForm from '../../components/add-orchard'
import { addOrchard, updateOrchard } from "../../redux/orchard/actions"

class PXAddOrchard extends React.Component {
  constructor(props) {
    super(props)
    
    this.state = {}
  }
  
  handleSubmit = async (formValues) => {
    if(!formValues.corporateId){
      addOrchard(formValues)
    } else {
      updateOrchard(formValues)
    }
  }
  
  render() {
    const {
      intl: {
        messages, locale
      },
    } = this.props
    
    const {
      terms,
      isLoading
    } = this.state
    
    return (
      <section className="forms-container with-bkg">
        <section className='new-user centered-page small-pages uk-container uk-height-1-1 uk-flex uk-flex-column uk-flex-center'>
          <div className='uk-width-2-3@m uk-width-1-1'>
            <PXAddOrchardForm
              onSubmit = {this.handleSubmit}
            />
          </div>
        </section>
      </section>
    )
  }
}

const mapStateToProps = state => ({})

const mapDispatchToProps = dispatch => ({
  addOrchard: data => dispatch(addOrchard(data)),
  updateOrchard: data => dispatch(updateOrchard(data)),
})

export default withRouter(injectIntl(connect(mapStateToProps, mapDispatchToProps)(PXAddOrchard)))