import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { injectIntl } from 'react-intl'
import { getLocalizedPath } from '../../utils/funcs'
import { Link } from 'react-router-dom'

import './menu.css'

class PXMainMenu extends React.Component {

  static propTypes = {
    intl: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired
  }
  render () {
    const {
      intl: { messages, locale },
      location,
      darkerMenu,
    } = this.props
  
    const languages = [{
      label: messages.language_de,
      value: 'de',
    }, 
    {
      label: messages.language_en,
      value: 'en',
    }]
  
    let currentLanguage = ''
    Object.values(languages).map((i, j) => {
      if (i.value === locale) {
        currentLanguage = i.label
      }
    })
    const currentUser = localStorage.getItem('currentUser') ? JSON.parse(localStorage.getItem('currentUser')) : null
    return (
      <ul className='main-menu'>
        <li className='menu-item'><Link to={getLocalizedPath('/', locale)}>{messages.menu.home}</Link></li>
        <li className='menu-item'>
          <a className="menu-item" type="button">{messages.menu.users}</a>
          <div uk-dropdown="mode: hover">
          <ul className="uk-nav uk-dropdown-nav submenu">
            <li><a href={getLocalizedPath('/users', locale)}>List Users</a></li>
            <li><a href={getLocalizedPath('/add_user', locale)}>Add User</a></li>
          </ul>
          </div>
        </li>
        <li className='menu-item'>
          <a className="menu-item" type="button">{messages.menu.corporates}</a>
          <div uk-dropdown="mode: hover">
          <ul className="uk-nav uk-dropdown-nav submenu">
            <li><a href={getLocalizedPath('/corporates', locale)}>{messages.menu.listCorporates}</a></li>
            <li><a href={getLocalizedPath('/add-corporate', locale)}>{messages.menu.addCorporate}</a></li>
          </ul>
          </div>
        </li>
        <li className='menu-item'>
          <a className="menu-item" type="button">{messages.menu.holdings}</a>
          <div uk-dropdown="mode: hover">
          <ul className="uk-nav uk-dropdown-nav submenu">
            <li><a href={getLocalizedPath('/holdings', locale)}>{messages.menu.listHoldings}</a></li>
            <li><a href={getLocalizedPath('/add-holding', locale)}>{messages.menu.addHolding}</a></li>
          </ul>
          </div>
        </li>
        <li className='menu-item'>
          <a className="menu-item" type="button">{messages.menu.farms}</a>
          <div uk-dropdown="mode: hover">
            <ul className="uk-nav uk-dropdown-nav submenu">
              <li><a href={getLocalizedPath('/farms', locale)}>{messages.menu.listFarms}</a></li>
              <li><a href={getLocalizedPath('/add-farm', locale)}>{messages.menu.addFarm}</a></li>
            </ul>
          </div>
        </li>

        {!currentUser && <li className="menu-item button"><a className="icon-item" href={getLocalizedPath('/login', locale)}>{messages.menu.login}</a></li>}

        {currentUser && currentUser.token && <li className="menu-item"><a className="icon-item" href={getLocalizedPath('/my_account', locale)}>{messages.menu.my_account}</a></li>}
        {currentUser && currentUser.token && <li className="menu-item"><a className="icon-item" href={getLocalizedPath('/logout', locale)}>{messages.menu.logout}</a></li>}

      </ul>
    )
  }
}
PXMainMenu.propTypes = {
  onLanguageChange: PropTypes.func,
  darkerMenu: PropTypes.bool,
}
  
const mapStateToProps = state => ({})
const mapDispatchToProps = dispatch => bindActionCreators({}, dispatch)

export default injectIntl(connect(mapStateToProps, mapDispatchToProps)(PXMainMenu))