import React from 'react'
import { Router, Switch } from 'react-router-dom'
import PublicRoute from './public-route'
import ProtectRoute from './protect-route'
import UserPrivateRoute from './user-private-route'
import ScrollToTop from '../components/scroll-to-top'
import languages from '../languages'
import { getLocalizedPath } from '../utils/funcs'

import Landing from '../pages/landing'
import NotFound from '../pages/not-found'
import Login from '../pages/login'
import Logout from '../pages/logout'
import ForgotPassword from '../pages/forgot-password'
import ResetPassword from '../pages/reset-password'
import PXAddUser from '../pages/add-user'
import PXUsers from '../pages/users'
import Imprint from '../pages/imprint'

import PXCorporateDetails from '../pages/corporate-details/corporate-details'
import PxCorporates from '../pages/corporates/corporates'
import PXAddCorporate from '../pages/add-corporate'

import PXHoldings from '../pages/holdings/holdings'
import PXAddHolding from '../pages/add-holding'

import PXAddFarm from '../pages/add-farm'

import PXAddOrchard from '../pages/add-orchard'

import PXWelcome from '../pages/welcome/welcome'

const createRoutes = (store) => {
  let routes = []

  Object.keys(languages).forEach((locale) => {
    const props = {
      store,
      locale,
    }

    routes = routes.concat([
      <PublicRoute key={`${locale}-home-route`} {...props} exact path={`/${locale}`} component={Landing} />,
      
      <ProtectRoute key={`${locale}-login-route`} {...props} path={getLocalizedPath('/login', locale)} component={Login} />,
      <ProtectRoute key={`${locale}-forgot-password-route`} {...props} path={getLocalizedPath('/forgot_password', locale)} component={ForgotPassword} />,
      <ProtectRoute key={`${locale}-reset-password-route`} {...props} path={getLocalizedPath('/reset_password', locale)} component={ResetPassword}  />,
      <ProtectRoute key={`${locale}-add-user-route`} {...props} path={getLocalizedPath('/add_user', locale)} component={PXAddUser} />,
      <ProtectRoute key={`${locale}-users-route`} {...props} path={getLocalizedPath('/users', locale)} component={PXUsers} />,
      
      <ProtectRoute key={`${locale}-corporate-details-route`} {...props} path={getLocalizedPath('/corporate_details', locale)} component={PXCorporateDetails} />,
      <ProtectRoute key={`${locale}-corporates-route`} {...props} path={getLocalizedPath('/corporates', locale)} component={PxCorporates} />,
      <ProtectRoute key={`${locale}-add-corporate-route`} {...props} path={getLocalizedPath('/add-corporate', locale)} component={PXAddCorporate} />,
      
      <ProtectRoute key={`${locale}-holdings-route`} {...props} path={getLocalizedPath('/holdings', locale)} component={PXHoldings} />,
      <ProtectRoute key={`${locale}-add-holding-route`} {...props} path={getLocalizedPath('/add-holding', locale)} component={PXAddHolding} />,
  
      <ProtectRoute key={`${locale}-add-farm-route`} {...props} path={getLocalizedPath('/add-farm', locale)} component={PXAddFarm} />,
  
      <ProtectRoute key={`${locale}-add-orchard-route`} {...props} path={getLocalizedPath('/add-orchard', locale)} component={PXAddOrchard} />,

      <ProtectRoute key={`${locale}-welcome-route`} {...props} path={getLocalizedPath('/welcome', locale)} component={PXWelcome} />,
      
      <PublicRoute key={`${locale}-imprint-route`} {...props} path={getLocalizedPath('/imprint', locale)} component={Imprint} />,
      <PublicRoute key={`${locale}-logout-route`} {...props} path={getLocalizedPath('/logout', locale)} component={Logout} hideHeader hideFooter />,

    ])
  })

  return routes
}

const routes = (history, store) => (
  <Router history={history}>
    <ScrollToTop>
      <Switch>
        <PublicRoute exact path="/" component={Landing} />,
        {createRoutes(store)}
        <PublicRoute exact path="*" component={NotFound} hideHeader hideFooter />,
      </Switch>
    </ScrollToTop>
  </Router>
)

export default routes
