import React from 'react'
import PropTypes from 'prop-types'
import {injectIntl} from 'react-intl'

import './heading.css'

import config from '../../../config'

class PXHeadingDetails extends React.Component {
  constructor(props) {
    super(props)
  }

  render () {
    const {
      details
    }= this.props

    return ( 
      <section className='heading'>
        <h1 className='title'>{details.cTitle}</h1>
        <div className='quick-details uk-flex'>
          <div className='uk-width-1-4'>
            <p className='first'>
              <span className='detail-label'>{details.farms.label}</span>
              <span className='detail-value'>{details.farms.value}</span>
            </p>
            <p className='second'>
              <span className='detail-label'>{details.orchads.label}</span>
              <span className='detail-value'>{details.orchads.value}</span>
            </p>
          </div>
          <div className='uk-width-1-4'>
            <p className='first'>
              <span className='detail-label'>{details.fruitTypes.label}</span>
              <span className='detail-value'>{details.fruitTypes.value}</span>
            </p>
            <p className='second'>
              <span className='detail-label'>{details.fruitVarieties.label}</span>
              <span className='detail-value'>{details.fruitVarieties.value}</span>
            </p>
          </div>
          <div className='uk-width-1-4'>
            <p className='first'>
              <span className='detail-label'>{details.users.label}</span>
              <span className='detail-value'>{details.users.value}</span>
            </p>
            <p className='second'>
              <span className='detail-label'>{details.area.label}</span>
              <span className='detail-value'>{details.area.value}</span>
            </p>
          </div>
        </div>
      </section>
    )
  }
}
PXHeadingDetails.propTypes = {
  details: PropTypes.object,
}
 
export default injectIntl(PXHeadingDetails)